import {createStore,combineReducers} from  'redux'

import locationCity from '@/redux/city_location/reducers.js'

import recommendData from '@/redux/recommend/reducers.js'

import switchState from '@/redux/switch_state/reducers.js'

const allReducers=combineReducers({
    locationCity,//城市数据
    recommendData,//线路数据
    switchState//切换数据
})

export default createStore(allReducers)