import { CITY } from '@/redux/city_location/variable.js'

let defaultCity={
    userId:Date.parse(new Date())+'',
    province:'重庆市',
    city:'重庆市',
    country:'--',
    longitude:null,
    latitude:null
}
export default function locationReducer( preStore = defaultCity , active){
    const { type , data } = active

    switch( type ){
        case CITY:
            return data;
        default:
            return preStore
    }
    
}