import React, {Component} from 'react'
import {BrowserRouter, Switch, Route, Redirect} from 'react-router-dom'
import asyncComponent from '@/tool/asyncComponent'
const index = asyncComponent(() => import("@/pages/index/index"))
const search = asyncComponent(()=>import("@/pages/search/index"))
const location = asyncComponent(()=>import('@/pages/location/index'))
const details = asyncComponent(()=>import('@/pages/details/index'))
const mero = asyncComponent(()=>import('@/pages/mero/index'))
const platform = asyncComponent(()=>import('@/pages/platform/index'))
const map = asyncComponent(()=>import('@/pages/map/index'))
const destination = asyncComponent(()=>import('@/pages/destination/index'))
export default class RouteConfig extends Component {
  render () {
    return (
      <BrowserRouter>
        <Switch>
          <Route path="/index" exact component= {index}/>
          <Route path="/search"  component= {search}/>
          <Route path="/details"  component= {details}/>
          <Route path="/mero"  component= {mero}/>
          <Route path="/location"  component= {location}/>
          <Route path="/platform"  component= {platform}/>
          <Route path="/map"  component= {map}/>
          <Route path="/destination"  component= {destination}/>
          <Redirect exact from='/' to='/index'/>
          <Route component= {index}/>
        </Switch>
      </BrowserRouter>
    )
  }
}